.navbar
{
    color: papayawhip;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 min(10rem,10vw);
    height: calc(5vh + 2rem);

}
#logo
{
    z-index: 10000000;
    position: fixed;
    top: 1.1em;
    left: clamp(1rem,10vh,10rem);
    height: clamp(2rem,5vh,5rem);
}
.nav-button>img
{
    z-index: 100000000;
    position: fixed;
    top: 1.1em;
    right: clamp(1rem,10vh,10rem);
    height: clamp(0.5rem,4vh,4rem);
}